.avatar-wrapper {
    margin-bottom: 8%;
    
}

.photo-block {
    max-width: 45%;
    margin: auto;

    img {
        width: 100%;
        border-radius: 100%;
    }
}

@media (max-width: 1280px) {

    .avatar-wrapper {
        margin-top: 14%;
    }

    .photo-block {
        max-width: 54%;
    }
}

@media (max-width: 1200px) {
    .photo-block {
        max-width: 55%;
    }
}


@media (max-width: 1025px) {
    .avatar-wrapper {
        margin-bottom: 3%;
        margin-top: 12%;
    }

    .photo-block {
        max-width: 23%;
        margin: auto;
    
        img {
            width: 100%;
            border-radius: 100%;
        }
    }
    
}

@media (max-width: 900px) {
.avatar-wrapper {
    margin-top: 15%;
}
}

@media (max-width: 800px)  {
   .photo-block {
    max-width: 25%;
} 

.avatar-wrapper {
    margin-top: 16%;
}
}

